import React, {
    useEffect,
    useState
} from 'react'
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { bindActionCreators } from 'redux';
import { notify } from '../../../Input-controls/Toast/Toast';
import { actions as Cartactions } from '../../../redux/ducks/cart';
import { actions as Stateactions } from '../../../redux/ducks/state_change';
import { urls } from '../../../routes/routes';
import { loadingIcoO } from '../../../utils/Utils';
import './ProxyRequest.scss'
function ProxyRequest(props) {

    const [data, setdata] = useState()
    const [buttonClick, setbuttonClick] = useState(false)

    useEffect(()=>{


        if(buttonClick && props.cart.isLoading == false){

        // try{
            if(props.cart.data.success === 1 )
            {
                props.history.push(urls.CHECKOUT);
            }
            else if(props.cart.data.success === 0 && !!props.cart.data.msg )
            {
              props.Close()
              try{
                notify.danger(props.cart.data.msg);
              }catch{
                notify.danger("Error while processing the request. We are looking into it.");
              }
            }
        //   }
        //   catch(e){
        //     notify.danger("Error while processing the request. We are looking into it.");
        //   }
        }
      },[props.cart])

    useEffect(() => {

        let data = {
            "plans": [{
                "plan_id": "magicpin_onyx_1a_v1",
                "offerings": "default",
                "gender": "all",
                "show_monthly": true,
                "pricing_frequency": "monthly",
                "age_range": props.rangedata.channelAgeRange,
                "target_product": "Healthsure",
                "total_employees": props.state.TotalEmployees
            }],
            "employeeCount": {
                "total_employees": props.state.TotalEmployees
            }
        }


        let planSelected={
            coverage_ids:[],
            plan_id:['magicpin_onyx_1a_v1'],
            selected_plan_id:'magicpin_onyx_1a_v1',
            pricing_name:'Magicsure Onyx',
            group_id:'magicpin_onyx_v1',
            policy_type:'1A',
            range:'18-35',
            demographicRange:[],
            features:{
              feature_short:[
                'Group Health Insurance of INR 1 Lakh',
                '1 free doctor teleconsultations. 1 for every 3 months of continued subscription',
                'Up to 20% discount on 3 medicine orders',
                'First three months sponsored by Magicpin'
              ]
            }
          }
        props.setPlan({...planSelected})
        props.setCart("ES", {
            ...data
        });
        setbuttonClick(true)
    }, [])

    return (
        <div>
            <>
                <div className="LoadingExoprtexcel noscroll overlay"></div>
                <div className="jdnijdiejiuie"> loading...</div> 
            </>
            <style jsx>
                {`
                body {
                    overflow: hidden;
                    }
                `}
            </style>
        </div>
    )
}

const mapStateToProps = (state) => {

    return {
      gstData:state.gst,
      rangedata:state.userstate,
      state:state.userstate.companyDetails,
      data:state.userstate.verify,
      searchCmpnyData: state.search_company,
      gstnUser:state.userstate.gstDetails,
      cart: state.cart,
      company_search_name: state.search_company.selectedCompany,
      mid:state.userstate.mid,
  }}
  
  const mapDispatchToProps = (dispatch) => ({
      ...bindActionCreators({
        ...Cartactions,
        ...Stateactions
          //ADD IF ITS FROM DIFFRENT ACTION FILE
      }, dispatch)
    });
  
  
  export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ProxyRequest))